
import { defineComponent, ref, reactive, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";

import { ElNotification } from "element-plus";
import logoImg from "@/assets/images/icons/png/bilab-black.png";
import bgImg from "@/assets/images/invite-confirm-background.jpeg";

import { getInvitation, postCreateMembers } from "@/apis/auth";
import { CreateMembersParams } from "@/typings/auth";

const Component = defineComponent({
  name: "InviteConfirm",
  setup() {
    const router = useRouter();
    const route = useRoute();

    const key = (route.query.key as string) || "";
    const step = ref(1);
    const isKeyPass = ref(false);

    const componentStyles = ref({
      backgroundImage: `url("${bgImg}")`
    });

    const form = reactive({
      username: "",
      brand: "",
      email: "",
      password: "",
      confirmPassword: ""
    });

    const init = async () => {
      try {
        const { data } = await getInvitation(key);
        form.brand = data.company.name;
        form.email = data.email;
        isKeyPass.value = true;
      } catch (e) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `${e.data.reason}`
        });
      }
    };

    onMounted(async () => await init());

    const onNextClick = () => {
      step.value = 2;
    };

    const onConfirmClick = async () => {
      if (/[^\w@+-.]/g.test(form.username)) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Username should follow rules: 150 characters or fewer. Letters, digits and @/./+/-/_ only`
        });
        return;
      }
      if (form.password !== form.confirmPassword) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Confirm password is not the same as password.`
        });
        return;
      }
      const params: CreateMembersParams = {
        username: form.username,
        password: form.password,
        email: form.email,
        url_seed: key
      };
      await postCreateMembers(params);
      await router.push({ name: "Root" });
    };

    return {
      step,
      isKeyPass,
      form,
      componentStyles,

      onNextClick,
      onConfirmClick,

      logoImg
    };
  }
});

export default Component;
